import React, { useEffect, useState } from "react";
//import { useParams, withRouter } from "react-router";
import { useParams } from 'react-router-dom';
import axios from "axios";
import useLocales from '../hooks/useLocales';
import Page from "../components/Page";
import { Box, Stack } from "@mui/material";
import { TwitterShareButton, FacebookShareButton, LinkedinShareButton, TelegramShareButton } from 'react-share';
import {
	FacebookIcon,
	LinkedinIcon,
	TelegramIcon,
} from "react-share";
import Iconify from "../components/Iconify";


const Blog = (props) => {
	const params = useParams();
	const { currentLang } = useLocales();
	const [users, setUsers] = useState({});
	const [currentLanguage, setCurrentLanguage] = useState(currentLang.value);
	console.log('value issss' + params);

	async function fetchData() {
		await axios.get(
			`https://backoffice.lobreauserge.com/api/blogbyidfr/${params.slug}`
		).then(
			(res) => setUsers(res.data)
		)
		setCurrentLanguage(currentLang.value);
	}

	const fetchTitle = (language, userObj) => {
		if (language === 'en') {
			return userObj.title;
		} else if (language === 'fr') {
			return userObj.titlefr;
		} else {
			return userObj.titlesp;
		}
	}

	const fetchContent = (language, userObj) => {
		if (language === 'en') {
			return userObj.description_front;
		} else if (language === 'fr') {
			return userObj.description_front_french;
		} else {
			return userObj.description_front_spanish;
		}
	}


	useEffect(() => {
		fetchData();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [currentLang]);
	const htmlString = fetchContent(currentLanguage, users)
	return (
		<Page title="Blog page" className="blog-page" sx={{ background: "#fff" }}>
			<Box className="blog-dv">
				<Box className="blog-image" sx={{ height: "400px" }}>
					{/* <img src={users.image ? users.image : "Loading . . . . "}  /> */}
					<Box component="img" src={users.image} sx={{ height: "100%", objectFit: { xs: "cover", sm: "contain" } }} alt="" />
				</Box>
				<Stack flexDirection="row" alignItems="center" justifyContent="center" my={2} sx={{ "&>button": { display: "flex", mr: 1 } }}>
					<TelegramShareButton url={"https://lobreauserge.com/blog/" + params.slug} >
						<TelegramIcon size="34" round="true" />
					</TelegramShareButton>
					<LinkedinShareButton url={"https://lobreauserge.com/blog/" + params.slug} >
						<LinkedinIcon size="34" round="true" />
					</LinkedinShareButton>
					<FacebookShareButton url={"https://lobreauserge.com/blog/" + params.slug} >
						<FacebookIcon size="34" round="true" />
					</FacebookShareButton>
					<TwitterShareButton url={"https://lobreauserge.com/blog/" + params.slug} >
						<Box sx={{
							width: "34px",
							height: "34px",
							backgroundColor: "#000",
							borderRadius: "50%",
							display: "flex",
							alignItems: "center",
							justifyContent: "center"
						}}>
							<Iconify icon="ri:twitter-x-fill" color="#fff" width="20px" height="20px" />
						</Box>
					</TwitterShareButton>
				</Stack>
				
				{/*<Box sx={{
					position: "relative",
					paddingBottom: "100%",
					height: "0",
				}}>
					<iframe src="https://www.tiktok.com/embed/7072819797184171310" title=" " allowfullscreen allow="encrypted-media;" frameborder="0" scrolling="no"
						style={{
							position: "absolute",
							top: "0",
							left: "0",
							width: "100%",
							height: "100%",
							borderRadius: "24px",
						}}>
						</iframe>
				</Box>*/}

				<Box className="blog-heading">
					{/* <h1>{users.title ? fetchTitle(currentLanguage, users) : "Loading . . . . "}</h1> */}
					<h2>{fetchTitle(currentLanguage, users)}</h2>
					<Box className="product-des" dangerouslySetInnerHTML={{ __html: htmlString }} />
					{/* <p >{users.description_front ? fetchContent(currentLanguage, users) : "Loading . . . . "}</p> */}
					{/* <p >{fetchContent(currentLanguage, users)}</p> */}
				</Box>
			</Box>
		</Page>
	);
};

export default Blog;